@import '../variables';

.product-block {
  width: 300px;
  text-align: center;
  margin-bottom: 65px;

  @media (width: 280px) and (width: 400px) {
    width: 260px;
  }

  &-wrapper {
    display: flex;
    justify-content: center;
    @media (max-width: 400px) {
      width: 200px;
    }
  }

  &__image {
    width: 260px;
    height: 390px;
    margin: 0 auto;

    @media (min-width: 350px) and (max-width: 775px) {
      width: 200px;
      height: revert;
    }
  }

  &__title {
    font-size: 20px;
    font-weight: 900;
    letter-spacing: 1%;
    margin-bottom: 5px;

    @media (max-width: 400px) {
      font-size: 14px;
    }
  }

  &__selector {
    display: flex;
    background-color: #f3f3f3;
    border-radius: 10px;
    flex-direction: column;
    padding: 6px;

    ul {
      display: flex;
      flex: 1;

      &:first-of-type {
        margin-bottom: 6px;
      }

      li,
      div {
        padding: 8px;
        flex: 1;
        cursor: pointer;
        font-weight: 600;
        font-size: 12px;
        @include noselect();
        &.active {
          background: #ffffff;
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
          border-radius: 5px;
          cursor: auto;
        }
      }
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
  }

  &__price {
    font-weight: bold;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.015em;

    @media (max-width: 400px) {
      font-size: 14px;
    }
  }
}
